export default {
    onShow(){
        if(this.orderData) {
            this.getthorderinfo(this.orderData)
        }
        
    },
    onLoad(option){
        console.log(option)
        if(option) {
            this.orderData = option
            this.getthorderinfo(option)
        }
        
    },
    data(){
        return {
            orderData:{},
            pplyinfo:{},
            applys:{},
            orgoods: {}
        }
    },
    methods: {
        getthorderinfo(data){
            this.$http.getthorderinfo(data).then(res=>{
                if(res.status == 200) {
                    this.pplyinfo = res.data
                    this.applys = res.data.applys
                    this.orgoods = res.data.orgoods
                }
            })
        },
        chexioaClick(){
            this.$showModal({content:'确认撤销申请吗？'}).then(result=>{
                if(result.confirm) {
                    this.$http.getchexiao({th_number:this.applys.th_number}).then(res=>{
                        if(res.status == 200) {
                            this.$toast(res.mess)
                            setTimeout(()=>{
                                this.getback()
                            },1000)
                        }
                    })
                }
            })
            
        },
        confirmShouhuo(){
            this.$showModal({content:'确认收货吗？'}).then(result=>{
                if(result.confirm) {
                    this.$http.getsurehuan({th_number:this.applys.th_number}).then(res=>{
                        if(res.status == 200) {
                            this.$toast(res.mess)
                            setTimeout(()=>{
                                this.getback()
                            },1000)
                        }
                    })
                }
            })
        }
    },
}
